import React from "react";

const Footer = () => {
  return (
    <footer className="main-footer">
      Powered by <strong>ISNE#5</strong>.
      <div className="float-right d-none d-sm-inline-block">
        <b>Version</b> 1.0
      </div>
    </footer>
  );
};

export default Footer;



